html, body {
  height: 95%;
  margin: 0;
}

body {
  color: #333;
  background-color: #f4f4f4;
  flex-direction: column;
  padding: 20px 20px 30px;
  font-family: sans-serif;
  display: flex;
}

h1 {
  color: #0056b3;
  text-align: center;
  margin-top: 0;
  margin-bottom: 10px;
}

.reference-link, .github-link {
  text-align: center;
  margin-bottom: 0;
}

.reference-link a, .github-link a {
  color: #007bff;
  text-decoration: none;
}

.reference-link a:hover, .github-link a:hover {
  text-decoration: underline;
}

.link-group {
  justify-content: center;
  gap: 20px;
  margin-bottom: 20px;
  display: flex;
}

.container {
  flex-direction: row;
  flex-grow: 1;
  align-items: stretch;
  gap: 20px;
  display: flex;
}

.input-section, .output-section {
  background-color: #fff;
  border-radius: 8px;
  flex-direction: column;
  flex: 1;
  min-width: 300px;
  height: 100%;
  padding: 20px;
  display: flex;
  box-shadow: 0 2px 4px #0000001a;
}

h2 {
  color: #0056b3;
  margin-top: 0;
  margin-bottom: 10px;
  padding-bottom: 10px;
}

details > summary {
  list-style: none;
}

details > summary::-webkit-details-marker {
  display: none;
}

summary h2 {
  border-bottom: 1px solid #eee;
  margin-right: 10px;
  display: inline-block;
}

summary {
  cursor: pointer;
  align-items: center;
  display: flex;
}

summary:before {
  content: "▶";
  margin-right: 10px;
  font-size: 1em;
}

details[open] > summary:before {
  content: "▼";
}

textarea {
  box-sizing: border-box;
  resize: vertical;
  border: 1px solid #ddd;
  border-radius: 4px;
  flex-grow: 1;
  width: 100%;
  height: 100%;
  margin-bottom: 10px;
  padding: 10px;
  font-family: monospace;
}

button {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 4px;
  margin-right: 10px;
  padding: 10px 15px;
  font-size: 16px;
}

button:hover {
  background-color: #0056b3;
}

.file-operations {
  justify-content: space-between;
  align-items: center;
  margin-top: 0;
  margin-bottom: 10px;
  display: flex;
}

.file-input-group {
  align-items: center;
  gap: 10px;
  display: flex;
}

.svg-operations {
  text-align: right;
  margin-top: 10px;
}

#svgOutput {
  background-color: #bbb;
  background-image: linear-gradient(45deg, #999 25%, #0000 25% 75%, #999 75%), linear-gradient(45deg, #999 25%, #0000 25% 75%, #999 75%);
  background-position: 0 0, 10px 10px;
  background-size: 20px 20px;
  border: 1px solid #ddd;
  border-radius: 4px;
  flex-grow: 1;
  height: 100%;
  padding: 10px;
}

.options-section {
  background-color: #fff;
  border-radius: 8px;
  margin-bottom: 20px;
  padding: 20px;
  box-shadow: 0 2px 4px #0000001a;
}

.option-group {
  align-items: center;
  gap: 10px;
  margin-bottom: 10px;
  display: flex;
}

.option-group label {
  min-width: 80px;
  font-weight: bold;
}

.option-group input[type="number"] {
  border: 1px solid #ddd;
  border-radius: 4px;
  width: 80px;
  padding: 8px;
}

.options-section button {
  margin-top: 10px;
}
